.appContainer{
    text-align: left;
}

.title{
    color: #f34a4a;
    padding-bottom: 2vh;
}

.subtitle{
    color: #F1D3BC;
}

.subtitle a{
    color: #F1D3BC;
}

.subtitle a:hover{
    color: #d8b397;
}

.split-left{
    height: 100vh;
}

.split-right{
    height: 100vh;
    background-color: #31383b;
}

@media only screen and (min-width: 1500px) {

    .title{
        font-size: 3vw;
        letter-spacing: 0.5vw;
    }

    .subtitle{
        font-size: 0.9vw;
        width: 40vw;
    }

}

@media only screen and (min-width: 801px) and (max-width: 1500px) {

    .title{
        font-size: 3vw;
        letter-spacing: 0.5vw;
    }

    .subtitle{
        font-size: 1.3vw;
    }

}

@media only screen and (min-width: 701px) and (max-width: 800px) {

    .titleContainer{
        text-align: center;
    }

    .title{
        font-size: 5vw;
        letter-spacing: 0.5vw;
        padding-bottom: 10vh;
    }

    .subtitle{
        font-size: 1.5vw;
        padding-bottom: 10vh;
    }

}

@media only screen and (min-width: 451px) and (max-width: 700px) {

    .titleContainer{
        text-align: center;
    }

    .title{
        font-size: 5vw;
        letter-spacing: 0.5vw;
        padding-bottom: 10vh;
    }

    .subtitle{
        font-size: 2.6vw;
        padding-bottom: 10vh;
    }

}

@media only screen and (max-width: 450px) {

    .titleContainer{
        text-align: center;
    }

    .title{
        font-size: 8vw;
        letter-spacing: 0.5vw;
        padding-bottom: 10vh;
    }

    .subtitle{
        font-size: 2.5vw;
        padding-bottom: 10vh;
    }

}